@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=ABeeZee&display=swap);
:root {
  --red: #ed5a5b;
  --white: #ffffff;
  --dark: rgba(46, 46, 46, 1);
  --dark-90: rgba(46, 46, 46, 0.9);
  --color-transition: color ease 0.3s;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0%;
  }
  100% {
    opacity: 100%;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0%;
  }
  100% {
    opacity: 100%;
  }
}

* {
  box-sizing: border-box; /* padding and border included within width, height */
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

body,
html {
  height: 100%;
  overflow: hidden; /* might change later when dealing w/ responsiveness */
}
body {
  font-family: "Lato", sans-serif;
  font-size: 18px;
  color: #ffffff;
  color: var(--white);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(rgba(46, 46, 46, 0.9), rgba(46, 46, 46, 0.9)),
    url("/static/media/BG Pic.07be3385.png") no-repeat;
  background: linear-gradient(var(--dark-90), var(--dark-90)),
    url("/static/media/BG Pic.07be3385.png") no-repeat;
  background-size: cover;
  -webkit-animation: fadeIn 2s ease;
          animation: fadeIn 2s ease;
}

a {
  text-decoration: none;
  color: #ffffff;
  color: var(--white);
}

.clearfix {
  overflow: auto;
}

.clearfix::after {
  content: "";
  clear: both;
  display: table;
}

.App {
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template-columns: minmax(60px, 1fr) repeat(6, minmax(50px, 190px)) minmax(
      60px,
      1fr
    );
  grid-template-rows: 120px 1fr 100px;
  grid-column-gap: 30px;
  -webkit-column-gap: 30px;
          column-gap: 30px;
}

.ContentContainer {
  overflow: hidden;
  grid-column: 2 / span 6;
  grid-row: 2 / 3;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

/* Hide scrollbar for Chrome, Safari and Opera */
.ContentContainer::-webkit-scrollbar {
  display: none;
}

#Home {
  height: 100%;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  text-align: center;
}

#Home p {
  font-family: "ABeeZee", sans-serif;
}

#Home img {
  height: 400px;
}

.Button {
  background-color: var(--red);
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Button:hover {
  cursor: pointer;
}

#nametag {
  font-weight: 700;
  font-size: 24px;
}

.Header {
  align-self: center;
  grid-column: 2 / span 6;
  grid-row: 1 / 2;
}

nav {
  display: flex;
  align-items: center;
}

.links {
  margin-left: auto;
}

.links a {
  margin-left: 50px;
}

.Footer {
  grid-column: 2 / span 6;
  grid-row: 3 / span 1;
  align-self: center;
}

.footerIcon {
  margin-right: 50px;
}

.footerIcon:hover {
  transition: var(--color-transition);
  color: var(--red);
}

.SideNav {
  grid-column: 8 / 9;
  grid-row: 2 / 3;
  align-self: center;
  justify-self: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.arrow,
.circle {
  margin-bottom: 5px;
}

.active {
  color: var(--red);
}

.circle {
  font-size: 10px;
}

.arrow:hover {
  transition: var(--color-transition);
  color: var(--red);
}

.SideNav a {
  scroll-behavior: smooth;
}

#About {
  height: 100%;
  margin-top: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

#About p {
  font-family: "ABeeZee", sans-serif;
}

.ResumeContainer {
  grid-column-end: 4;
  justify-self: end;
}

.ResumeContainer a {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ResumeContainer img {
  height: 400px;
}

.projectsButton {
  grid-column-start: 2;
  justify-self: start;
  margin: auto;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.projectsButton p {
  margin-bottom: 5px;
}

.projectsButton:hover {
  cursor: pointer;
}

.skillContainer {
  height: 50%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.skillCol {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.skillItem {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.AboutContent {
  display: flex;
  flex-direction: column;
}

#Projects {
  height: 100%;
  width: 100%;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-content: center;
}

#projectRow {
  height: 80%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-content: end;
  grid-column-gap: 30px;
  -webkit-column-gap: 30px;
          column-gap: 30px;
}

.ProjectContainer {
  height: 100%;
  justify-self: stretch;
  grid-column: auto / span 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
}

.ProjectBlock {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: bottom;
  overflow: hidden;
}

#acnh {
  background-image: url(/static/media/acnh-1.fb1e3467.gif);
  background-size: contain;
}

#dl {
  background-image: url(/static/media/dl-imaging-preview.e3c9d8d6.jpg);
  background-size: contain;
}

.ProjectBlock:hover {
  cursor: pointer;
}

.teitter-timeline {
  overflow: hidden;
}

.contactButton {
  margin: auto;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.contactButton p {
  margin-bottom: 5px;
}

.contactButton:hover {
  cursor: pointer;
}

.projectTitle {
  margin-top: 5px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.projectTitle p {
  margin-right: 5px;
}

.projectTitle a:hover {
  color: var(--red);
  transition: var(--color-transition);
}

#Contact {
  margin-top: 10px;
  height: 100%;
  margin-top: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.ContactContainer {
  background-color: var(--white);
  height: 80%;
  margin-top: 20px;
}

.contactForm {
  grid-column-start: 2;
  background-color: var(--light);
  height: 80%;
  margin-top: 20px;
  color: var(--white);
  justify-items: end;
}

input[type="submit"] {
  width: 25%;
  float: right;
  height: 10%;
  background-color: var(--red);
  border: none;
}

input[type="submit"]:hover {
  cursor: pointer;
}

input {
  width: 100%;
  border: none;
  padding: 5px 0px;
  border-bottom: 2px solid var(--white);
  background-color: var(--light);
  margin-bottom: 10vh;
  color: var(--white);
}

input:focus {
  color: var(--white);
}

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px var(--light) inset;
}

.contactSubmit {
  grid-column-start: 2;
  justify-self: end;
  width: 25%;
  height: 30%;
}

label {
  padding-bottom: 20px;
  display: inline-block;
}

.contactInfo {
  height: 80%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.contactLink {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.contactLink p {
  margin-left: 10px;
}

